/*
 * CSS that will be loaded inside the WordPress editor
 */
.wp-block-gutenberg-starter-simple__image {
  position: relative;
  padding: 0; }

.wp-block-gutenberg-starter-simple figure {
  position: static;
  padding: 50% 0 0 0;
  margin: 0;
  background: #ccc;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start; }
  .wp-block-gutenberg-starter-simple figure img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; }
  .wp-block-gutenberg-starter-simple figure button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .wp-block-gutenberg-starter-simple figure figcaption {
    margin-left: 10px;
    margin-bottom: 10px;
    width: 100%;
    padding: 10px;
    background: rgba(239, 239, 239, 0.7); }
